export interface AuthorizationOptions {
    message?: string;
    uniq?: boolean;
}

export enum PromptSettingType {
    /**
     * 不提醒
     */
    none = 0,
    /**
     * 首次提醒
     */
    first = 1,
    /**
     * 每次都提醒
     */
    every = 2,
    /**
     * 每次都提醒，且不可跳过
     */
    everyAndNotSkip = 3,
}

export interface PromptSettingConfig {
    promptPaymentPasswordSettingWhenSign?: PromptSettingType | boolean; // 0: 不提醒， 1: 首次提醒， 2: 每次都提醒, 3: 每次都提醒，且不可跳过
    promptMasterPasswordSettingWhenLogin?: PromptSettingType | boolean; // 0: 不提醒， 1: 首次提醒， 2: 每次都提醒， 3: 每次都提醒，且不可跳过
}
