import { addHexPrefix, isHexString } from '@ethereumjs/util';
import { providerErrors, rpcErrors } from '@metamask/rpc-errors';
import {
    EvmRpcMethod,
    ethereumUtils,
    isNullish,
    requireNotNullish,
    type ConnectParam,
    type EIP1193Provider,
    type EIP1559TxData,
    type MessageTypes,
    type PrefixedHexString,
    type RpcInput,
    type SignTypedDataVersion,
    type TxData,
    type TypedDataV1,
    type TypedMessage,
} from '@particle-network/auth-core';
import { chains } from '@particle-network/chains';

export class EVMProvider implements EIP1193Provider {
    signMessage = async (message: PrefixedHexString | string, uniq?: boolean): Promise<string> => {
        throw new Error('signMessage not implemented.');
    };

    sendTransaction = async (transaction: Omit<TxData | EIP1559TxData, 'from'>): Promise<string> => {
        throw new Error('sendTransaction not implemented.');
    };

    signTypedData = async <V extends SignTypedDataVersion, T extends MessageTypes>({
        data,
        version,
        uniq,
    }: {
        data: V extends 'V1' ? TypedDataV1 : TypedMessage<T>;
        version: V;
        uniq?: boolean;
    }): Promise<string> => {
        throw new Error('signTypedData not implemented.');
    };

    get selectedAddress(): string | null {
        return requireNotNullish(window.particleAuth).ethereum.selectedAddress;
    }

    get chainId(): string {
        return requireNotNullish(window.particleAuth).ethereum.chainId;
    }

    isConnected(): boolean {
        return requireNotNullish(window.particleAuth).ethereum.isConnected();
    }

    on(event: string, listener: (...args: any[]) => void): this {
        window.particleAuth?.ethereum.on(event, listener);
        return this;
    }

    once(event: string, listener: (...args: any[]) => void): this {
        window.particleAuth?.ethereum.once(event, listener);
        return this;
    }

    off(event: string, listener: (...args: any[]) => void): this {
        window.particleAuth?.ethereum.off(event, listener);
        return this;
    }

    removeListener(event: string, listener: (...args: any[]) => void): this {
        window.particleAuth?.ethereum.removeListener(event, listener);
        return this;
    }

    /**
     * 适配 [web3modal](https://github.com/WalletConnect/web3modal)
     * @param event
     */
    private emit(event: string) {
        if (event === 'disconnect') {
            this.disconnect();
        }
    }

    /**
     * @deprecated user `request` instead
     */
    async enable(): Promise<string[]> {
        return await requireNotNullish(window.particleAuth).ethereum.enable();
    }

    async disconnect() {
        return await requireNotNullish(window.particleAuth).ethereum.disconnect();
    }

    async connect(param?: ConnectParam): Promise<string[]> {
        return await requireNotNullish(window.particleAuth).ethereum.connect(param);
    }

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    async request(data: Partial<RpcInput>): Promise<any> {
        const method = data.method;
        if (!method) {
            throw rpcErrors.invalidParams('Invalid parameters: method is required.');
        }
        if (Object.values(EvmRpcMethod).includes(method as EvmRpcMethod)) {
            if (method === EvmRpcMethod.ethAccounts || method === EvmRpcMethod.ethRequestAccounts) {
                return await requireNotNullish(window.particleAuth).ethereum.request(data);
            } else if (method === EvmRpcMethod.ethChainId) {
                return this.chainId;
            } else if (method.startsWith(EvmRpcMethod.ethSignTypedData)) {
                this.#checkConnected();
                /**
                 * v1: https://medium.com/metamask/scaling-web3-with-signtypeddata-91d6efc8b290
                 * v3: https://medium.com/metamask/eip712-is-coming-what-to-expect-and-how-to-use-it-bb92fd1a7a26
                 * v4: https://docs.metamask.io/wallet/reference/eth_signtypeddata_v4/
                 */
                const isV1 = ethereumUtils.isSignTypedDataV1(method);
                const from = isV1 ? data?.params?.[1] : data?.params?.[0];
                let typedData = isV1 ? data?.params?.[0] : data?.params?.[1];
                if (!from || typeof from !== 'string') {
                    throw rpcErrors.invalidParams('Invalid parameters: must provide an Ethereum address.');
                }
                if (!typedData) {
                    throw rpcErrors.invalidParams('Invalid parameters: must provide typed data.');
                }
                if (from.toLowerCase() !== this.selectedAddress?.toLowerCase()) {
                    throw providerErrors.unauthorized();
                }
                if (typeof typedData === 'string') {
                    typedData = JSON.parse(typedData);
                }
                const uniq = ethereumUtils.isUniq(method);
                const version = ethereumUtils.getSignTypedVersion(method);

                const result = await this.signTypedData({
                    data: typedData as any, // eslint-disable-line
                    version,
                    uniq,
                });
                return result;
            } else if (method.startsWith(EvmRpcMethod.personalSign)) {
                this.#checkConnected();
                let message = data?.params?.[0];
                const from = data?.params?.[1];
                if (!message || typeof message !== 'string') {
                    throw rpcErrors.invalidParams('Invalid parameters: must provide a valid message.');
                }
                if (!from || typeof from !== 'string') {
                    throw rpcErrors.invalidParams('Invalid parameters: must provide an Ethereum address.');
                }
                if (from.toLowerCase() !== this.selectedAddress?.toLowerCase()) {
                    throw providerErrors.unauthorized();
                }

                if (!isHexString(message)) {
                    message = addHexPrefix(Buffer.from(message).toString('hex'));
                }
                const uniq = ethereumUtils.isUniq(method);
                const result = await this.signMessage(message as PrefixedHexString, uniq);
                return result;
            } else if (method === EvmRpcMethod.walletSwitchEthereumChain) {
                return await requireNotNullish(window.particleAuth).ethereum.request(data);
            } else if (method === EvmRpcMethod.ethSendTransaction) {
                this.#checkConnected();
                const chainId = Number(data.chainId || this.chainId);
                const chain = chains.getEVMChainInfoById(chainId);
                if (!chain) {
                    throw providerErrors.chainDisconnected();
                }
                const txData = data.params?.[0] as any; // eslint-disable-line
                if (!txData) {
                    throw rpcErrors.invalidParams('Invalid parameters: must provide an transaction data.');
                }
                if (isNullish(txData.type)) {
                    if (chains.isChainSupportEIP1559(chain)) {
                        // set transaction default type "0x2", EIP1559.
                        txData.type = '0x2';
                    } else {
                        txData.type = '0x0';
                    }
                }
                if (isNullish(txData.chainId)) {
                    txData.chainId = `0x${chainId.toString(16)}`;
                }
                if (isNullish(txData.nonce)) {
                    txData.nonce = '0x0';
                }
                if (isNullish(txData.data)) {
                    txData.data = '0x';
                }
                const result = await this.sendTransaction(txData);
                return result;
            } else {
                throw providerErrors.unsupportedMethod();
            }
        } else {
            return await requireNotNullish(window.particleAuth).ethereum.request(data);
        }
    }

    #checkConnected() {
        if (!this.isConnected()) {
            throw providerErrors.unauthorized();
        }
    }
}
