import { PreferenceKey, once } from '../repository';
import { PromptSettingType } from './model/bundle';

export const isPromptSetMasterPassword = (promptMasterPasswordSettingWhenLogin?: number | boolean) => {
    if (
        promptMasterPasswordSettingWhenLogin === PromptSettingType.every ||
        promptMasterPasswordSettingWhenLogin === PromptSettingType.everyAndNotSkip
    ) {
        return true;
    } else if (
        promptMasterPasswordSettingWhenLogin ||
        promptMasterPasswordSettingWhenLogin === PromptSettingType.first
    ) {
        return !once(PreferenceKey.PN_OPEN_SET_MASTER_PASSWORD);
    } else {
        return false;
    }
};
