import {
    AuthCoreEvent,
    AuthType,
    SolanaRpcMethod,
    check,
    getLatestAuthType,
    isConnected,
    particleAuth,
    requireNotNullish,
} from '@particle-network/auth-core';
import { chains, type ChainInfo } from '@particle-network/chains';
import type { Transaction, VersionedTransaction } from '@solana/web3.js';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useCustomNavigate, useEvents, useModalOptions, useParticleAuth } from '..';
import { AuthError } from '../../api/model/authError';
import { AuthPage } from '../../components/customRouter';
import { isClient } from '../../utils';
import { getSolanaChain } from '../../utils/chain-utils';
import { SolanaWallet } from '../solanaWallet';
import { AuthCoreModalEvent } from '../types';
import { useAuthCore } from './useAuthCore';
import { useConnect } from './useConnect';
import { useUserInfo } from './useUserInfo';

export const useSolana = () => {
    const navigate = useCustomNavigate();

    const { events } = useEvents();

    const [chainId, setChainId] = useState<number>(particleAuth.solana.chainId);

    const [chainInfo, setChainInfo] = useState<ChainInfo>(
        requireNotNullish(getSolanaChain(particleAuth.solana.chainId))
    );

    const [address, setAddress] = useState(particleAuth.solana.selectedAddress);

    const { setAuthCoreModal } = useParticleAuth();

    const { connect } = useConnect();

    const { userInfo } = useUserInfo();

    const { needRestoreWallet } = useAuthCore();

    const { modalOptions } = useModalOptions();

    const silentApprove = useMemo(() => {
        if (
            needRestoreWallet ||
            userInfo?.security_account.has_set_payment_password ||
            modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign ||
            getLatestAuthType() !== AuthType.jwt
        ) {
            return false;
        }
        return true;
    }, [
        userInfo?.security_account.has_set_payment_password,
        needRestoreWallet,
        modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign,
    ]);

    useEffect(() => {
        const onChainChanged = (chainId: number) => {
            setChainId(chainId);
        };
        particleAuth.solana.on('chainChanged', onChainChanged);

        const onConnect = () => {
            setAddress(particleAuth.solana.selectedAddress);
        };
        particleAuth.solana.on('connect', onConnect);

        setChainId(particleAuth.solana.chainId ?? 101);

        const onDisconnect = () => {
            setAddress(null);
        };
        particleAuth.solana.on('disconnect', onDisconnect);

        const onRequestAccount = ({ resolve, reject, param }: any) => {
            connect(param)
                .then((userInfo) => {
                    resolve(userInfo);
                })
                .catch((error) => {
                    reject(error);
                });
        };
        particleAuth.on(AuthCoreEvent.RequestSolanaAccount, onRequestAccount);

        return () => {
            particleAuth.solana.off('chainChanged', onChainChanged);
            particleAuth.solana.off('connect', onConnect);
            particleAuth.solana.off('disconnect', onDisconnect);
            particleAuth.off(AuthCoreEvent.RequestSolanaAccount, onRequestAccount);
        };
    }, [connect]);

    useEffect(() => {
        if (chainId) {
            setChainInfo(requireNotNullish(getSolanaChain(chainId)));
        }
    }, [chainId]);

    const checkAuthCoreState = () => {
        check(particleAuth, 'Please init first!');
        check(isConnected(), AuthError.notLogin());
        check(particleAuth.solana.selectedAddress, 'Please create wallet first!');
    };

    const signImpl = useCallback(
        async <T>(state: { method: string; param: unknown; chainId?: number }): Promise<T> => {
            checkAuthCoreState();
            const result = await new Promise<T>((resolve, reject) => {
                if (events.listenerCount(AuthCoreModalEvent.SignResponse) > 0) {
                    reject(AuthError.pending('sign'));
                } else {
                    events.once(AuthCoreModalEvent.SignResponse, (response) => {
                        if (response.error) {
                            reject(response.error);
                        } else {
                            resolve(response.result);
                        }
                        setAuthCoreModal({ particleModalVisible: false });
                    });
                    navigate(AuthPage.SolanaSign, {
                        replace: true,
                        state,
                    });
                }
            });
            return result;
        },
        [navigate]
    );

    const signMessage = useCallback(
        async (message: Uint8Array): Promise<Uint8Array> => {
            if (silentApprove) {
                const signature = await particleAuth.solana.signMessage(message);
                return signature;
            } else {
                const signature = await signImpl<Uint8Array>({
                    param: message,
                    method: SolanaRpcMethod.signMessage,
                });
                return signature;
            }
        },
        [signImpl, silentApprove]
    );

    const signTransaction = useCallback(
        async <T extends Transaction | VersionedTransaction>(transaction: T, chainId?: number): Promise<T> => {
            check(!chainId || chains.getSolanaChainInfoById(chainId), `The chainId: ${chainId} is not supported`);
            if (silentApprove) {
                const signature = await particleAuth.solana.signTransaction(transaction, chainId);
                return signature;
            } else {
                const signature = await signImpl<T>({
                    param: transaction,
                    method: SolanaRpcMethod.signTransaction,
                    chainId,
                });
                return signature;
            }
        },
        [signImpl, silentApprove]
    );

    const signAllTransactions = useCallback(
        async <T extends Transaction | VersionedTransaction>(transactions: T[], chainId?: number): Promise<T[]> => {
            check(!chainId || chains.getSolanaChainInfoById(chainId), `The chainId: ${chainId} is not supported`);
            if (silentApprove) {
                const signature = await particleAuth.solana.signAllTransactions(transactions, chainId);
                return signature;
            } else {
                const signature = await signImpl<T[]>({
                    param: transactions,
                    method: SolanaRpcMethod.signAllTransactions,
                    chainId,
                });
                return signature;
            }
        },
        [signImpl, silentApprove]
    );

    const signAndSendTransaction = useCallback(
        async <T extends Transaction | VersionedTransaction>(transaction: T, chainId?: number): Promise<string> => {
            check(!chainId || chains.getSolanaChainInfoById(chainId), `The chainId: ${chainId} is not supported`);
            if (silentApprove) {
                const signature = await particleAuth.solana.signAndSendTransaction(transaction, chainId);
                return signature;
            } else {
                const signature = await signImpl<string>({
                    param: transaction,
                    method: SolanaRpcMethod.signAndSendTransaction,
                    chainId,
                });
                return signature;
            }
        },
        [signImpl, silentApprove]
    );

    const switchChain = useCallback(async (id: number) => {
        await requireNotNullish(particleAuth, 'Please init first!').solana.switchChain(id);
    }, []);

    const enable = useCallback(async (): Promise<string> => {
        await requireNotNullish(particleAuth, 'Please init first!').solana.connect();
        return requireNotNullish(particleAuth.solana.selectedAddress);
    }, []);

    const wallet = useMemo(() => {
        if (isClient() && window.particle?.solana) {
            return window.particle?.solana;
        } else {
            const solanaWallet = new SolanaWallet();
            solanaWallet.signMessage = signMessage;
            solanaWallet.signTransaction = signTransaction;
            solanaWallet.signAllTransactions = signAllTransactions;
            solanaWallet.signAndSendTransaction = signAndSendTransaction;

            if (isClient()) {
                if (window.particle) {
                    window.particle.solana = solanaWallet;
                } else {
                    window.particle = { solana: solanaWallet };
                }
            }

            return solanaWallet;
        }
    }, []);

    useEffect(() => {
        if (wallet) {
            wallet.signMessage = signMessage;
            wallet.signTransaction = signTransaction;
            wallet.signAllTransactions = signAllTransactions;
            wallet.signAndSendTransaction = signAndSendTransaction;
        }
    }, [wallet, signMessage, signTransaction, signAllTransactions, signAndSendTransaction]);

    return {
        address,
        chainId,
        chainInfo,
        switchChain,
        signMessage,
        signTransaction,
        signAllTransactions,
        signAndSendTransaction,
        enable,
        wallet,
    };
};
