import { getLatestAuthType, isSocialAuthType } from '@particle-network/auth-core';
import { useEffect } from 'react';
import { useConnect, useEthereum } from './hooks';

/**
 * https://github.com/WalletConnect/web3modal/
 */
const Web3ModalProvider = ({ web3Modal }: { web3Modal?: any }) => {
    const { connectionStatus } = useConnect();
    const { provider } = useEthereum();

    useEffect(() => {
        if (web3Modal && connectionStatus === 'connected' && isSocialAuthType(getLatestAuthType()) && provider) {
            //https://github.com/WalletConnect/web3modal/blob/8109853/packages/ethers/src/client.ts#L560
            web3Modal?.setEIP6963Provider?.(provider, 'Particle Wallet');
        }
    }, [connectionStatus, provider, web3Modal]);
    return undefined;
};

export default Web3ModalProvider;
