import {
    AuthCoreEvent,
    AuthType,
    EvmRpcMethod,
    SignTypedDataVersion,
    check,
    getLatestAuthType,
    isConnected,
    particleAuth,
    requireNotNullish,
    type EIP1559TxData,
    type MessageTypes,
    type PrefixedHexString,
    type TxData,
    type TypedDataV1,
    type TypedMessage,
} from '@particle-network/auth-core';
import { chains, type ChainInfo } from '@particle-network/chains';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useCustomNavigate, useEvents, useModalOptions, useParticleAuth } from '..';
import { AuthError } from '../../api/model/authError';
import { AuthPage } from '../../components/customRouter';
import { isClient } from '../../utils';
import { getEvmChain } from '../../utils/chain-utils';
import { toHexPrefixString } from '../../utils/common-utils';
import evmSendTransaction from '../../utils/evmSendTransaction';
import { EVMProvider } from '../evmProvider';
import { AuthCoreModalEvent } from '../types';
import { useAuthCore } from './useAuthCore';
import { useConnect } from './useConnect';
import { useUserInfo } from './useUserInfo';

export const useEthereum = () => {
    const navigate = useCustomNavigate();

    const { events } = useEvents();

    const [chainId, setChainId] = useState<number>(Number(particleAuth.ethereum.chainId));

    const [chainInfo, setChainInfo] = useState<ChainInfo>(requireNotNullish(getEvmChain(chainId)));

    const [address, setAddress] = useState(particleAuth.ethereum.selectedAddress);

    const { userInfo } = useUserInfo();

    const { needRestoreWallet } = useAuthCore();

    const { modalOptions } = useModalOptions();

    const { setAuthCoreModal } = useParticleAuth();

    const { connect } = useConnect();

    const silentApprove = useMemo(() => {
        if (
            needRestoreWallet ||
            userInfo?.security_account.has_set_payment_password ||
            modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign ||
            getLatestAuthType() !== AuthType.jwt
        ) {
            return false;
        }
        return true;
    }, [
        userInfo?.security_account.has_set_payment_password,
        needRestoreWallet,
        modalOptions.promptSettingConfig?.promptPaymentPasswordSettingWhenSign,
    ]);

    useEffect(() => {
        const onChainChanged = (chainId: string) => {
            setChainId(Number(chainId));
        };
        particleAuth.ethereum.on('chainChanged', onChainChanged);

        const onConnect = () => {
            setAddress(particleAuth.ethereum.selectedAddress);
        };
        particleAuth.ethereum.on('connect', onConnect);

        const onAccountsChanged = (accounts: string[]) => {
            if (accounts[0]) {
                setAddress(accounts[0]);
            }
        };
        particleAuth.ethereum.on('accountsChanged', onAccountsChanged);

        const onDisconnect = () => {
            setAddress(null);
        };
        particleAuth.ethereum.on('disconnect', onDisconnect);

        const onRequestAccount = ({ resolve, reject, param }: any) => {
            connect(param)
                .then((userInfo) => {
                    resolve(userInfo);
                })
                .catch((error) => {
                    reject(error);
                });
        };
        particleAuth.on(AuthCoreEvent.RequestEthereumAccount, onRequestAccount);

        return () => {
            particleAuth.ethereum.off('chainChanged', onChainChanged);
            particleAuth.ethereum.off('connect', onConnect);
            particleAuth.ethereum.off('accountsChanged', onAccountsChanged);
            particleAuth.ethereum.off('disconnect', onDisconnect);
            particleAuth.off(AuthCoreEvent.RequestEthereumAccount, onRequestAccount);
        };
    }, [connect]);

    useEffect(() => {
        if (chainId) {
            setChainInfo(requireNotNullish(getEvmChain(chainId)));
        }
    }, [chainId]);

    const checkAuthCoreState = () => {
        check(particleAuth, 'Please init first!');
        check(isConnected(), AuthError.notLogin());
        check(particleAuth.ethereum.selectedAddress, 'Please create wallet first!');
    };

    const signImpl = useCallback(
        async <T>(state: { method: string; param: unknown }): Promise<T> => {
            checkAuthCoreState();
            const result = await new Promise<T>((resolve, reject) => {
                if (events.listenerCount(AuthCoreModalEvent.SignResponse) > 0) {
                    reject(AuthError.pending('sign'));
                } else {
                    events.once(AuthCoreModalEvent.SignResponse, (response) => {
                        if (response.error) {
                            reject(response.error);
                        } else {
                            resolve(response.result);
                        }
                        setAuthCoreModal({ particleModalVisible: false });
                    });
                    navigate(AuthPage.EvmSign, {
                        replace: true,
                        state,
                    });
                }
            });
            return result;
        },
        [navigate, events]
    );

    const signMessage = useCallback(
        async (message: PrefixedHexString | string, uniq?: boolean): Promise<string> => {
            if (silentApprove) {
                const result = await particleAuth.ethereum.signMessage(toHexPrefixString(message), uniq);
                return result;
            } else {
                const result = await signImpl<string>({
                    param: toHexPrefixString(message),
                    method: uniq ? EvmRpcMethod.personalSignUniq : EvmRpcMethod.personalSign,
                });
                return result;
            }
        },
        [signImpl, silentApprove]
    );

    const signTypedData = useCallback(
        async <V extends SignTypedDataVersion, T extends MessageTypes>({
            data,
            version,
            uniq,
        }: {
            data: V extends 'V1' ? TypedDataV1 : TypedMessage<T>;
            version: V;
            uniq?: boolean;
        }): Promise<string> => {
            if (silentApprove) {
                const result = await particleAuth.ethereum.signTypedData({ data, version, uniq });
                return result;
            } else {
                const result = await signImpl<string>({
                    param: data,
                    method: getSignTypedDataMethod({ version, uniq }),
                });
                return result;
            }
        },
        [signImpl, silentApprove]
    );

    const sendTransaction = useCallback(
        async (transaction: Omit<TxData, 'from'> | Omit<EIP1559TxData, 'from'>): Promise<string> => {
            if (transaction.chainId && !chains.getEVMChainInfoById(Number(transaction.chainId))) {
                throw new Error(`The chainId: ${transaction.chainId} is not supported.`);
            }

            if (silentApprove) {
                const result = await evmSendTransaction(transaction);
                return result;
            } else {
                const result = await signImpl<string>({
                    param: { ...transaction, from: address, chainId: transaction.chainId || chainId },
                    method: EvmRpcMethod.ethSendTransaction,
                });
                return result;
            }
        },
        [address, chainId, signImpl, silentApprove]
    );

    const switchChain = useCallback(async (id: number | PrefixedHexString) => {
        await requireNotNullish(particleAuth, 'Please init first!').ethereum.switchChain(id);
    }, []);

    const enable = useCallback(async (): Promise<string> => {
        const result = await requireNotNullish(particleAuth, 'Please init first!').ethereum.enable();
        return result[0];
    }, []);

    const provider = useMemo((): EVMProvider => {
        if (isClient() && window.particle?.ethereum) {
            return window.particle?.ethereum;
        } else {
            const eip1193Provider = new EVMProvider();
            eip1193Provider.signMessage = signMessage;
            eip1193Provider.signTypedData = signTypedData;
            eip1193Provider.sendTransaction = sendTransaction;
            if (isClient()) {
                if (window.particle) {
                    window.particle.ethereum = eip1193Provider;
                } else {
                    window.particle = { ethereum: eip1193Provider };
                }
            }
            return eip1193Provider;
        }
    }, []);

    useEffect(() => {
        if (provider) {
            provider.signMessage = signMessage;
            provider.signTypedData = signTypedData;
            provider.sendTransaction = sendTransaction;
        }
    }, [provider, signMessage, signTypedData, sendTransaction]);

    useEffect(() => {
        //https://eips.ethereum.org/EIPS/eip-6963
        if (provider && typeof window !== 'undefined') {
            const info = {
                uuid: 'cf589558-848b-4b1b-9ee7-800b5bbc07da',
                name: 'Particle Wallet',
                icon: 'https://static.particle.network/logo-small.png',
                rdns: 'network.particle',
            };
            const announceProvider = () => {
                window.dispatchEvent(
                    new CustomEvent('eip6963:announceProvider', {
                        detail: Object.freeze({
                            provider,
                            info,
                        }),
                    })
                );
            };
            window.addEventListener('eip6963:requestProvider', announceProvider);
            announceProvider();
            return () => {
                window.removeEventListener('eip6963:requestProvider', announceProvider);
            };
        }
    }, [provider]);

    return {
        provider,
        address,
        chainId,
        chainInfo,
        switchChain,
        signMessage,
        signTypedData,
        sendTransaction,
        enable,
    };
};

function getSignTypedDataMethod({ version, uniq }: { version: SignTypedDataVersion; uniq?: boolean }): string {
    if (SignTypedDataVersion.V1 === version) {
        return EvmRpcMethod.ethSignTypedDataV1;
    } else if (SignTypedDataVersion.V3 === version) {
        return EvmRpcMethod.ethSignTypedDataV3;
    } else {
        return uniq ? EvmRpcMethod.ethSignTypedDataV4Uniq : EvmRpcMethod.ethSignTypedDataV4;
    }
}
