import { RecordType } from '@particle-network/analytics';
import type { Authorization, ConnectParam, ConnectWithSocialParam, SocialAuthType } from '@particle-network/auth-core';
import { analyticsRecord, connect, thirdpartyAuth } from '@particle-network/auth-core';
import { type Chain } from '@particle-network/chains';
import base64url from 'base64url';
import { useCallback } from 'react';
import { useCustomNavigate, useParticleAuth } from '..';
import { isPromptSetMasterPassword } from '../../api/master-password';
import { AuthPage } from '../../components/customRouter';
import { sendAnalyticsActive } from '../../utils/sendAnalyticsActive';
import { AuthCoreModalEvent } from '../types';

const useLogin = () => {
    const navigate = useCustomNavigate();
    const { loginSuccessRedirectToApp, modalOptions, events } = useParticleAuth();
    const loginHandler = useCallback(
        async (data: ConnectParam, emitError: boolean = true) => {
            try {
                if (!data.chain && modalOptions?.wallet?.customStyle?.supportChains?.length) {
                    data.chain = modalOptions?.wallet?.customStyle?.supportChains[0];
                }
                const userInfo = await connect(data);
                const { token = '', ...restInfo } = userInfo;
                sendAnalyticsActive('login', restInfo);

                analyticsRecord({
                    // eslint-disable-next-line camelcase
                    record_type: RecordType.PAGE_LOGIN_BUTTON_CLICK_SUCCESS, // 登录成功
                });
                console.log('login successful, check master password');

                if (userInfo.security_account?.has_set_master_password) {
                    console.log('login successful, start verify master password');
                    navigate(AuthPage.MasterPasswordVerify, {
                        replace: true,
                        state: {
                            loginVerifyMasterPassword: true,
                        },
                    });
                } else {
                    if (
                        isPromptSetMasterPassword(
                            modalOptions.promptSettingConfig?.promptMasterPasswordSettingWhenLogin
                        )
                    ) {
                        console.log('login successful, popup set master password');
                        navigate(AuthPage.MasterPasswordPrompt, {
                            replace: true,
                        });
                    } else {
                        loginSuccessRedirectToApp();
                    }
                }
            } catch (error) {
                analyticsRecord({
                    // eslint-disable-next-line camelcase
                    record_type: RecordType.PAGE_LOGIN_BUTTON_CLICK_FAILURE, // 登录失败
                });
                if (emitError) {
                    console.log('login failed, emit LoginFail event', error);
                    events.emit(AuthCoreModalEvent.LoginFail, error);
                } else {
                    throw error;
                }
            }
        },
        [
            modalOptions.promptSettingConfig,
            navigate,
            loginSuccessRedirectToApp,
            modalOptions?.wallet?.customStyle?.supportChains,
        ]
    );

    const socialAuthLogin = useCallback(
        async (
            options: ConnectWithSocialParam & {
                authorization?: Authorization;
                chain?: Chain;
            }
        ) => {
            try {
                const appState = base64url(
                    JSON.stringify({
                        chain: options.chain,
                        authorization: options.authorization,
                        socialType: options.socialType,
                    })
                );
                await thirdpartyAuth({
                    authType: options.socialType as SocialAuthType,
                    appState,
                    prompt: options.prompt,
                });
            } catch (error) {
                events.emit(AuthCoreModalEvent.LoginFail, error);
            }
        },
        [events]
    );

    return { loginHandler, socialAuthLogin };
};

export default useLogin;
